@import "base/general";
@import "base/fonts";
@import "base/variables";
@import "base/mixins";
@import "base/typography";
@import "components/sprite";

@import "helpers/variables";
@import "helpers/mixins";
@import "helpers/functions";

@import "vendor/libs";
@import "vendor/normalize";

@import "../blocks/modules";

@import "sections/header";
@import "sections/block-book";

$light-color: #fff;
$accent-color: #65e496;
$gray-color: #9191b5;
$bg-light-color: #f5f6fa;
$bg-dark-color: #44444f;

h1,
h2,
h3,
h4,
p {
  padding: 0;
  margin: 0;
}

h3 {
  font-size: 24px;
}

body {
  font-family: "Roboto Slab", serif;
  font-size: 15px;
  line-height: 24px;
}

.page {
  &__tittle {
    font-size: 48px;
    text-align: center;
    margin-bottom: 25px;
    letter-spacing: 2px;
  }
  &__subtittle {
    color: $accent-color;
    text-align: center;
    font-size: 18px;
    letter-spacing: 0.9px;
  }
  &--grey {
    padding-top: 142px;
    background-color: $bg-light-color;
    .block-about-text {
      p {
        letter-spacing: 0.8px;
      }
    }
  }
}
.page-main {
  background-image: url("../img/Bg/top-section-bg.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}

.wrap {
  max-width: 1170px;
  margin: 0 auto;
}

.container {
  padding: 0;
  margin: 0;
}

.block-text {
  max-width: calc(97% - 500px);
  color: $light-color;
  &--width {
    flex-basis: calc(93% - 500px);
  }
  &__tittle {
    font-size: 48px;
    font-weight: 700;
    letter-spacing: 1.8px;
    margin-bottom: 10px;
    span {
      display: block;
      color: $accent-color;
      font-size: 18px;
      margin-top: 10px;
      letter-spacing: 0.9px;
    }
  }
  &__subtittle {
    display: block;
    color: $accent-color;
    font-size: 18px;
    margin-bottom: 45px;
  }
  &__name {
    font-family: "Open Sans";
    font-size: 18px;
    letter-spacing: 0.7px;
    margin-bottom: 30px;
  }
  &__description {
    font-family: "Open Sans";
    font-size: 15px;
    line-height: 24px;
    margin-bottom: 24px;
    letter-spacing: 0.7px;
    vertical-align: 50px;
  }
  &__btn {
    display: block;
    color: #fff;
    text-decoration: none;
    max-width: 140px;
    border-radius: 3px;
    text-align: center;
    font-size: 18px;
    margin-top: 45px;
    margin-bottom: 20px;
    padding: 11px 0 15px 0;
    background: linear-gradient(to top left, #65e49b, #65e4b0);
    letter-spacing: 0.7px;
    max-width: 250px;
  }
  small {
    font-family: "Open Sans";
    font-size: 12px;
    font-style: italic;
  }
  &-item {
    margin-bottom: 55px;
    &__date {
      font-size: 15px;
      font-family: "Open Sans";
      letter-spacing: 0.8px;
      margin-bottom: 4px;
    }
    &__tittle {
      font-size: 18px;
      letter-spacing: 0.9px;
      margin-bottom: 16px;
    }
    &__desc {
      font-size: 15px;
      font-family: "Open Sans";
      letter-spacing: 0.75px;
    }
    &__btn {
      display: block;
      color: #fff;
      text-decoration: none;
      border-radius: 3px;
      text-align: center;
      font-size: 18px;
      margin-top: 45px;
      margin-bottom: 20px;
      padding: 11px 0 15px 0;
      background: linear-gradient(to top left, #65e49b, #65e4b0);
      letter-spacing: 0.7px;
    }
  }
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 50px;
  padding-bottom: 160px;
  &__logo {
    display: block;
    width: 137px;
    height: 28px;
    background-image: url("../img/Logo/Header-logo.png");
    background-repeat: no-repeat;
  }
  &-list {
    list-style-type: none;
    display: flex;
    justify-content: flex-end;
    &__item {
      font-family: "Open Sans";
      font-size: 16px;
      margin-left: 22px;
      padding-bottom: 7px;
      border-bottom: 3px solid transparent;
      transition: 0.3s;
      &.active {
        border-bottom: 3px solid $accent-color;
      }
      &:hover {
        border-bottom: 3px solid $accent-color;
      }
    }
    &__link {
      text-decoration: none;
      color: $light-color;
      letter-spacing: 0.5px;
    }
  }
}

.block-book {
  display: flex;
  justify-content: flex-start;
  padding-bottom: 110px;
  &__text {
    color: $light-color;
    .block-text__name {
      margin-bottom: 41px;
    }
    .block-text__tittle {
      margin-bottom: 25px;
      letter-spacing: 1px;
    }
    .block-text__subtittle {
      margin-top: 0;
      margin-bottom: 40px;
      letter-spacing: 1px;
    }
    &block-text__btn {
      margin-bottom: 14px;
      background: linear-gradient(to top left, #65e49b, #65e4b0);
      letter-spacing: 0.7px;
      box-shadow: 0 -2px #58b988 inset;
    }
  }
  &-demo {
    display: block;
    width: 447px;
    height: 643px;
    background-image: url("../img/Books/book.png");
    background-repeat: no-repeat;
    flex-basis: 500px;
    padding-left: 0;
    margin-left: 0;
    padding-bottom: 0;
    position: relative;
    &__tittle {
      font-size: 36px;
      position: absolute;
      top: 122px;
      left: 133px;
      letter-spacing: 0.7px;
      text-transform: uppercase;
      span {
        font-family: "Open Sans";
        font-size: 14px;
        color: $accent-color;
        display: block;
        position: absolute;
        top: 33px;
        text-transform: none;
        width: 225px;
      }
    }
    &__author {
      font-family: "Open Sans";
      font-size: 14px;
      color: $accent-color;
      position: absolute;
      bottom: 52px;
      left: 88px;
      letter-spacing: 0.7px;
    }
  }
}

.block-author {
  display: flex;
  justify-content: space-between;
  padding-top: 90px;
  &--1 {
    padding-top: 175px;
  }
  &--2 {
    margin-bottom: 147px;
  }
  .block-text {
    color: #000;
    &__name {
      margin-bottom: 32px;
    }
    &__tittle {
      margin-bottom: 42px;
      span {
        margin-top: 24px;
        margin-bottom: 0;
      }
    }
    &__btn {
      max-width: 240px;
      background: linear-gradient(to top left, #65e49b, #65e4b0);
      letter-spacing: 0.7px;
      box-shadow: 0 -2px #b2f1be inset;
    }
  }
  .block-text--width {
    margin-left: 168px;
  }

  .block-ava {
    &__img {
      width: 438px;
      height: 466px;
      position: absolute;
      padding-top: 50px;
    }
    &__svg {
      padding-left: 47px;
    }
    &__img--left {
      width: 435px;
      height: 606px;
      padding-top: 49px;
      padding-left: 50px;
    }
    &__svg--left {
      padding-left: 0;
    }
  }
}

.block-counter {
  margin-top: 170px;
  margin-bottom: 160px;
  .wrap {
    display: flex;
    justify-content: space-around;
  }
  p {
    font-family: "Open Sans";
    text-align: center;
    font-size: 18px;
    span {
      font-family: "Roboto Slab";
      display: block;
      font-weight: 100;
      font-size: 80px;
      color: $accent-color;
      margin-bottom: 45px;
    }
  }
}

.block-about {
  display: flex;
  justify-content: space-between;
  margin-top: 82px;
  padding-bottom: 230px;
  h3 {
    letter-spacing: 1.2px;
    margin-bottom: 20px;
  }
  p {
    font-family: "Open Sans";
    letter-spacing: 0.6px;
  }
  &-text {
    flex-basis: 41%;
    &__btn {
      width: 135px;
      margin-top: 35px;
      border: none;
      background: linear-gradient(to top left, #65e49b, #65e4b0);
      letter-spacing: 0.7px;
      box-shadow: 0 -2px #b2f1be inset;
    }
    .feature {
      display: flex;
      margin-top: 30px;
      height: 25px;
      align-items: center;
      vertical-align: middle;
      .feature-tittle {
        font-family: "Roboto Slab";
        font-weight: 700;
        letter-spacing: 3px;
      }
      &-list {
        display: flex;
        list-style-type: none;
        padding-left: 23px;
        padding-top: 5px;
        &__item {
          margin-right: 12px;
        }
        &__icon {
          font-size: 18px;
          color: $accent-color;
        }
      }
    }
  }
  &-table {
    flex-basis: 49%;
    &__list {
      margin: 25px 0 0 0;
      padding: 0;
      counter-reset: list;
      column-count: 2;
      & > li {
        position: relative;
        padding-left: 37px;
        counter-increment: list;
        list-style-type: none;
        margin-bottom: 8px;
        letter-spacing: 0.7px;
        &::before {
          content: counter(list) ". ";
        }
        &::after {
          content: "+";
          font-family: "Open Sans";
          font-size: 15px;
          line-height: 22px;
          position: absolute;
          top: 1px;
          left: 0;
          background-color: #5fc9b2;
          color: $light-color;
          padding: 0px 7px;
        }
      }
    }
  }
}

.other-books {
  padding-top: 160px;
  .page-tittle {
    margin-top: 155px;
  }
  &__image {
    margin-bottom: 25px;
  }
  &__list {
    display: flex;
    justify-content: space-around;
    list-style-type: none;
    text-align: center;
    padding-top: 75px;
    padding-left: 0;
  }
  &__item {
    .block-about__text {
      p {
        font-family: "Open Sans";
        font-size: 15px;
        margin-top: 15px;
        max-width: 330px;
        margin-bottom: 25px;
      }
    }
  }
  &__link {
    color: $accent-color;
    text-transform: uppercase;
    text-decoration: none;
  }
  .block-text__btn {
    margin-left: auto;
    margin-right: auto;
    margin-top: 75px;
    max-width: 220px;
    background: linear-gradient(to top left, #65e49b, #65e4b0);
    letter-spacing: 0.7px;
    box-shadow: 0 -2px #b2f1be inset;
  }
}

.block-text--width {
  margin-left: 168px;
  padding-top: -12px;
}

.block-text-item__btn {
  max-width: 135px;
  margin-top: 27px;
}

.block-text {
  margin-left: 0;
}

.block-testimotionals {
  background-image: url("../img/Bg/testimotionals-bg.png");
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: 0 0;
}

.block-testimotionals {
  width: 100%;
  height: 695px;
  margin-top: 160px;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  padding-top: 225px;
  .block-about-text {
    max-width: 1000px;
    text-align: center;
    margin-top: 20px;
    margin-left: auto;
    margin-right: auto;
    .block-text__tittle {
      font-size: 24px;
      color: #65e496;
      letter-spacing: 0.8px;
      margin-bottom: 5px;
    }
    .block-text__name {
      font-size: 15px;
      letter-spacing: 0px;
      color: #9191b5;
    }
    .block-text__description {
      max-width: 760px;
      margin-left: auto;
      margin-right: auto;
      margin-top: 30px;
      color: #9191b5;
    }
    .block-text__nav {
      margin-top: 60px;
    }
  }
}

.block-blog {
  display: flex;
  justify-content: space-around;
  h4 {
    font-size: 18px;
    letter-spacing: 1px;
    margin-bottom: 5px;
  }
  &-img {
    width: 369px;
    height: 224px;
    overflow: hidden;
    position: relative;
    margin-bottom: 15px;
    img {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      object-fit: cover;
    }
  }
  &-info {
    font-family: "Open Sans";
    font-size: 15px;
    margin-bottom: 10px;
    a {
      color: $accent-color;
      text-decoration: none;
    }
  }
  &__desc {
    font-family: "Open Sans";
    font-size: 15px;
    max-width: 400px;
    letter-spacing: 0.7px;
  }
  &-page__btn {
    display: block;
    margin: 84px auto 0;
    padding: 13px 0;
    max-width: 265px;
  }
}
.page-block {
  padding-bottom: 160px;
  .page__tittle,
  .page__subtittle {
    display: block;
  }
  .page__subtittle {
    margin-bottom: 85px;
    letter-spacing: 1.1px;
  }
}

.block-letter {
  display: flex;
  justify-content: space-between;
  margin-top: 160px;
  margin-bottom: 155px;
  &-item {
    flex-basis: 49%;
  }
  &__tittle {
    text-align: left;
    letter-spacing: 1.9px;
    margin-top: 8px;
  }
  &__text {
    font-family: "Open Sans";
    font-size: 15px;
    letter-spacing: 0.75px;
  }
  &__input {
    box-sizing: border-box;
    width: 100%;
    height: 48px;
    background-color: $bg-dark-color;
    border: none;
    border-radius: 3px;
    font-family: "Open Sans";
    font-weight: 700;
    letter-spacing: 0.6px;
    color: $gray-color;
    padding-left: 30px;
    margin-bottom: 20px;
    &::placeholder {
      font-family: "Open Sans";
      font-weight: 700;
      letter-spacing: 0.6px;
      color: $gray-color;
    }
  }
  &__btn {
    display: block;
    color: #fff;
    text-decoration: none;
    width: 100%;
    border: none;
    border-radius: 3px;
    text-align: center;
    font-size: 18px;
    padding: 11px 0 15px 0;
    background: linear-gradient(to top left, #65e49b, #65e4b0);
    letter-spacing: 0.7px;
    box-shadow: 0 -2px #b2f1be inset;
  }
}

.footer {
  background-color: $bg-dark-color;
  color: $gray-color;
  padding: 155px 0;
}

.block-footer {
  display: flex;
  justify-content: space-between;
  font-family: "Open Sans";
  & > div {
    max-width: 375px;
  }
  & > p {
    letter-spacing: 1px;
  }

  &__logo {
    display: block;
    width: 137px;
    height: 28px;
    background-image: url("../img/Logo/Footer-logo.png");
    margin-bottom: 25px;
    & > p {
      max-width: 365px;
    }
  }
  &__tittle {
    font-family: "Roboto Slab";
    font-size: 18px;
    color: $accent-color;
    text-transform: uppercase;
    margin-bottom: 21px;
    padding-top: 7px;
  }
  &__list {
    list-style-type: none;
    padding-left: 0;
    line-height: 26px;
    letter-spacing: 1.5px;
    margin: 0;
  }
  &__social {
    display: flex;
    list-style-type: none;
    padding-left: 0;
    font-size: 15px;
    & > li {
      margin-right: 12px;
      & > a {
        color: $accent-color;
      }
    }
  }
  &__copyright {
    margin-top: 25px;
  }
}
