.page-main {
	background-image: url('../img/Bg/top-section-bg.jpg');
	background-repeat: no-repeat;
	background-size: cover;
	padding-bottom: 50px; }
.header {
	padding-top: 38px;
	display: flex;
	justify-content: space-between;
	align-items: center;
	max-width: 1170px;
	margin: 0 auto;
	padding-bottom: 166px;
	&__logo {
		display: block;
		width: 137px;
		height: 28px;
		background-image: ('../img/Logo/Header-logo.png'); }
	&-list {
		color: #fff;
		font-family: 'Open Sans', serif;
		font-size: 16px;
		list-style-type: none;
		display: flex;
		justify-content: flex-end;
		&__item {
			margin-left: 22px;
			padding-bottom: 5px;
			border-bottom: 3px solid transparent;
			transition: 0.3s;
			&:hover {
				border-bottom: 3px solid #65e496; } }
		&__item.active {
			border-bottom: 3px solid #65e496; }
		&__link {
			color: #FFF;
			text-decoration: none;
			letter-spacing: 0.5px; } } }
