.block {
	&-book {
		display: flex;
		justify-content: space-between;
		color: #fff;
		&-demo {
			display: block;
			width: 447px;
			height: 643px;
			flex-basis: 500px;
			margin-left: 30px;
			padding-bottom: 162px; } }
	&-text {
		margin-left: 55px;
		flex-basis: calc(100% - 500px);
		&__tittle {
			margin-bottom: 15px;
			span {
				display: block; } }
		&__subtittle {
			margin-top: 10px; }
		&__name {
			margin-top: 0px;
			margin-bottom: 30px; }
		&__description {
			margin-bottom:  24px;
			max-width: 588px; }
		&__btn {
			display: block;
			padding-top: 15px;
			padding-bottom: 15px;
			max-width: 250px;
			margin-top: 45px;
			margin-bottom: 19px; }
		& small {
			font-style: italic; } } }
